  .btn--home{
    position: relative;
    display: inline-flex;
    width: 180px;
    height: 55px;
    margin: 0 15px;
    perspective: 1000px;
  }
  .btn--home a{
    font-size: 19px;
    letter-spacing: 1px;
    transform-style: preserve-3d;
    transform: translateZ(-25px);
    transition: transform .25s;
    font-family: 'Montserrat', sans-serif;
    
  }
  .btn--home a:before,
  .btn--home a:after{
    position: absolute;
    content: "Get Started";
    height: 55px;
    width: 180px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 5px solid black;
    box-sizing: border-box;
    border-radius: 5px;
    font-weight: bold;
  }
  .btn--home a:before{
    color: #fff;
    background: #000;
    transform: rotateY(0deg) translateZ(25px);
  }
  .btn--home a:after{
    color: #fff;
    transform: rotateX(90deg) translateZ(25px);
  }
  .btn--home a:hover{
    transform: translateZ(-25px) rotateX(-90deg);
  }






  
