#pricing {
 
  background: #142F43;
  width: 100%;
  height: 100%;
  font-family: 'Montserrat';
  overflow-y: hidden;
}

#pricing .period{
  font-weight: 800;
}

.pricing .card {
  border: none;
  border-radius: 1rem;
  transition: all 0.2s;
  box-shadow: 0 0.5rem 1rem 0 rgba(0, 0, 0, 0.1);
  
}

.pricing hr {
  margin: 1.5rem 0;
}

.pricing .card-title {
  margin: 0.5rem 0;
  font-size: 0.9rem;
  letter-spacing: .1rem;
  font-weight: bold;
}

.pricing .card-price {
  font-size: 3.5rem;
  margin: 0;
}

.pricing .card-price .period {
  font-size: 1rem;
}

.pricing ul li {
  margin-bottom: 0.5rem;
  text-align: center;
  list-style: none;
  font-weight: bold;
  font-size: 18px;
}

.pricing .text-muted {
  opacity: 0.7;
}



/* Hover Effects on Card */

@media (min-width: 992px) {
  .pricing .card:hover {
    margin-top: -.25rem;
    margin-bottom: .25rem;
    box-shadow: 0 0.5rem 1rem 0 rgba(0, 0, 0, 0.3);
    transition: all 0.3s ease;
  }

  .pricing .card:hover .btn {
    opacity: 1;
  }
}


