#loginform{
    background: #007bff;
    background: linear-gradient(to right, #0062E6, #33AEFF);
  }
  
  .card-img-left {
    width: 45%;
    /* Link to your background image using in the property below! */
    background: scroll center url('../src/pexels-instawalli-169789.jpg');
    background-size: cover;
  }
  
  .btn-login {
    font-size: 0.9rem;
    letter-spacing: 0.05rem;
    padding: 0.75rem 1rem;
    border-radius: 50px;
  }
  
  .btn-google {
    color: white !important;
    background-color: #ea4335;
    border-radius: 50px;
  }
  
  .btn-facebook {
    color: white !important;
    background-color: #3b5998;
    border-radius: 50px;
  }
  
  #loginform input{
      border-radius: 50px;
  }